import "../Cart/CartStyle.scss";
import { NavLink } from "react-router-dom";
import {  useHistory } from "react-router-dom";

const Cart = (props) => {
  window.scrollTo({ top: 0 });

  let handleDelete = props.handleDelete;
  let datamap = props.datamap;

  let total = new Number();
  let end = new Number();
  let history = useHistory()

const handleClickHere=()=>{
  history.push("/product")
}
  return (
    <div className="cart-container">
      <div className="grid wide">
        <div className="link-product">
          <NavLink activeClassName="active" exact={true} to="/">
            Trang ch
          </NavLink>
          &nbsp;/&nbsp;
          <NavLink activeClassName="active" to="/product">
            Sn phm
          </NavLink>
          &nbsp;/&nbsp;<span>Gi hng</span>
        </div>
        {datamap && datamap.length > 0 && (
          <div className="cart">
            <div className="cart-title row">
              <h1 className=" col l-6 m-6 c-12">Gi hng</h1>
              <div className=" quality col l-1 m-1 c-0">Gi</div>
              <div className=" quality col l-1 m-1 c-0">S lng</div>
              <div className=" total col l-4 m-4 c-0">Tng cng</div>
            </div>
            {datamap &&
              datamap.length > 0 &&
              datamap.map((item) => {
                let totelItem = parseInt(item.price) * parseInt(item.quality);
                total = total + totelItem;
                end = total + 40000;
                return (
                  <div className="cart-main row">
                    <div className=" cart-img col l-2 m-2 c-4">
                      <img alt="sdas" src={item.image} />
                    </div>
                    <div className="cart-content col l-4 m-4 c-8">
                      <h3>{item.name}</h3>
                      <p>{item.content}</p>
                    </div>
                    <div className="cart-price col l-1 m-1 c-2">
                      <p>{parseInt(item.price).toLocaleString()} VND</p>
                    </div>
                    <div className=" cart-input-quality col l-1 m-1 c-2">
                      <p>{item.quality}</p>
                    </div>
                    <div className=" cart-delete-total col l-4 m-4 c-8">
                      <button onClick={() => handleDelete(item.id)}>
                        Loi b
                      </button>
                      <span>{totelItem.toLocaleString()} VN</span>
                    </div>
                  </div>
                );
              })}

            <div className="cart-total row">
              <div className="col l-6 m-6 c-12">
                <div className="total-content">
                  <div className="total-left">
                    <p>Tm tnh</p>
                    <p>Ph ship</p>
                    <h4>Tng</h4>
                  </div>
                </div>
                <div className="total-right">
                  <p>{total.toLocaleString()}VN</p>
                  <p>40,000VN</p>
                  <h4>{end.toLocaleString()}VN</h4>
                </div>
              </div>
              <button>Thanh Toan</button>
            </div>
          </div>
        )}
        {datamap.length === 0 &&
         <div className="cart">
         <p>Gi hng ang trng</p>
         <p> tip tc mua sm xin hy n vo <a onClick={()=>handleClickHere()}>y</a></p>
          <img src='https://gomsu.tk/static/media/empty-cart.5a32a825a6a1db625dca01dba24fcf4c.svg'/>

         </div>
         }
      </div>
    </div>
  );
};
export default Cart;
